import Vue from 'vue'
import App from './App.vue'
import router from '@/router'
import store from '@/store'

import '@/store/restore'
import '@/config/plugins'

import { github, gitee } from '@/config/count'

Vue.config.productionTip = false

const doCount = store => {
  if (store.state.site === 'gitee') gitee()
  else github()
}

new Vue({
  router,
  store,
  render: h => h(App),
  beforeMount () {
    doCount(this.$store)
  }
}).$mount('#app')
