<template>
    <span v-html="content"></span>
</template>

<script>
export default {
  name: 'i18n',
  props: {
    path: String
  },
  data () {
    return {
      content: null
    }
  },
  methods: {
    loadValue () {
      let value = null
      if (this.path) {
        let arr = this.path.split('.'); let i18n = this.$store.state.i18n
        for (let prop of arr) {
          if (prop) value = value ? value[prop] : i18n[prop]
        }
      }
      this.content = value
    }
  },
  watch: {
    '$store.state.language' (val) {
      this.loadValue()
    }
  },
  mounted () {
    this.loadValue()
  }
}
</script>

