export const keys = {
  Home: 'Home',
  vPage: 'v-page',
  vDialogs: 'v-dialogs',
  vUploader: 'v-uploader',
  vRegion: 'v-region',
  vSelectPage: 'v-selectpage',
  vSuggest: 'v-suggest',
  vPlayBack: 'v-playback',
  vSelectMenu: 'v-selectmenu',
  vGallery: 'v-gallery'
  /* 'vTableGrid': 'v-tablegrid', */
}

export const urls = [
  {
    home: '/',
    demo: '/',
    key: keys.Home
  }, {
    home: '/page',
    demo: '/page/demo',
    key: keys.vPage
  }, {
    home: '/dialog',
    demo: '/dialog/demo',
    key: keys.vDialogs
  }, {
    home: '/upload',
    demo: '/upload/demo',
    key: keys.vUploader
  }, {
    home: '/region',
    demo: '/region/demo',
    key: keys.vRegion
  }, {
    home: '/selectpage',
    demo: '/selectpage/demo',
    key: keys.vSelectPage
  }, {
    home: '/suggest',
    demo: '/suggest/demo',
    key: keys.vSuggest
  }, {
    home: '/playback',
    demo: '/playback/demo',
    key: keys.vPlayBack
  }, {
    home: '/selectmenu',
    demo: '/selectmenu/demo',
    key: keys.vSelectMenu
  }, {
    home: '/gallery',
    demo: '/gallery/demo',
    key: keys.vGallery
  }
]

const HOME_PAGE = '/'

export const getProject = path => {
  if (!path) return
  return urls.find(val => {
    return (path === val.home && val.home === HOME_PAGE) || (val.home !== HOME_PAGE && path.startsWith(val.home))
  })
}

export const getProjectByUrl = function (path) {
  if (!path) {
    const site = window.location
    path = site.hash.substring(1)// ignore # code
  }
  path = path.toLowerCase()
  return urls.find(val => {
    return path === val.home || path === val.demo || path === `/changelog/${val.key}`
  })
}

export const getProjectByKey = function (key) {
  if (!key) return null
  return urls.find(val => val.key.toLowerCase() === key.toLowerCase())
}

